.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: var(--spacing-lg);
}

.modal-container {
    background-color: var(--color-bg-primary);
    border-radius: var(--radius-lg);
    max-width: 800px;
    width: 100%;
    max-height: 90vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    box-shadow: var(--shadow-lg);
}

.modal-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--spacing-lg);
    border-bottom: 1px solid var(--color-bg-input);
}

.modal-header-spacer {
    width: 40px;
}

.modal-title {
    flex: 1;
    text-align: center;
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-medium);
    color: var(--color-text-primary);
    margin: 0;
}

.modal-content {
    padding: var(--spacing-lg);
    overflow-y: auto;
    flex: 1;
}

@media (max-width: 768px) {
    .modal-backdrop {
        padding: 0;
    }

    .modal-container {
        max-height: 100vh;
        border-radius: 0;
        height: 100%;
    }
}
