.weather-container {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    margin: 0 auto;
    gap: var(--spacing-lg);
    justify-content: center;
    padding: var(--spacing-lg);
}

.weather-top-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--spacing-lg);

}

.weather-loading,
.weather-error {
    text-align: center;
    padding: var(--spacing-lg);
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
}

@media (max-width: 768px) {
    .weather-container {
        padding: var(--spacing-md);
    }

    .weather-top-row {
        flex-direction: column;
        align-items: flex-start;
        gap: var(--spacing-md);
    }
}
