.activity-row {
    display: flex;
    align-items: center;
    gap: var(--spacing-md);
    padding: var(--spacing-md) 0;
    border-bottom: 1px solid var(--color-bg-input);
}

.activity-icon {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.activity-icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: grayscale(0);
}

.activity-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;
    gap: var(--spacing-sm);
}

.activity-name {
    font-size: var(--font-size-md);
    color: var(--color-text-primary);
    font-weight: var(--font-weight-normal);
}

.activity-value {
    font-size: var(--font-size-md);
    font-weight: var(--font-weight-bold);
    text-align: right;
}

.quality-good {
    color: #4ade80;
}

.quality-moderate {
    color: #fbbf24;
}

.quality-bad {
    color: #ef4444;
}
