.map-modal-content {
    height: 500px;
    position: relative;
}

.map-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.map-layer-control {
    position: absolute;
    bottom: 20px;
    left: 20px;
    z-index: 1000;
}

.layer-control-button,
.layer-control-option {
    display: flex;
    align-items: center;
    width: 100%;
    padding: var(--spacing-sm) var(--spacing-md);
    background-color: var(--color-bg-primary);
    color: var(--color-text-primary);
    font-size: var(--font-size-sm);
    border: 2px solid var(--color-bg-input);
    cursor: pointer;
    transition: var(--transition-normal);
}

.layer-control-button {
    min-width: 180px;
    box-shadow: var(--shadow-lg);
}

.layer-control-button:hover {
    background-color: var(--color-bg-secondary);
    border-color: var(--color-primary);
}

.layer-control-menu {
    position: absolute;
    bottom: calc(100% + 8px);
    left: 0;
    background-color: var(--color-bg-primary);
    border: 2px solid var(--color-bg-input);
    box-shadow: var(--shadow-lg);
    min-width: 180px;
    overflow: hidden;
}

.layer-control-option {
    border: none;
    border-bottom: 1px solid var(--color-bg-input);
    justify-content: flex-start;
}

.layer-control-option:hover {
    background-color: var(--color-bg-secondary);
    color: var(--color-text-primary);
}

.layer-control-option:last-child {
    border-bottom: none;
}

.layer-control-option.active {
    background-color: var(--color-primary);
    color: white;
    font-weight: var(--font-weight-medium);
}

.leaflet-control-zoom {
    border: 2px solid var(--color-bg-input) !important;
    border-radius: var(--radius-md) !important;
    overflow: hidden;
}

.leaflet-control-zoom a {
    background-color: var(--color-bg-primary) !important;
    color: var(--color-text-primary) !important;
    border-bottom: 1px solid var(--color-bg-input) !important;
}

.leaflet-control-zoom a:hover {
    background-color: var(--color-primary) !important;
    color: white !important;
    transition: var(--transition-normal);
}

@media (max-width: 768px) {
    .map-modal-content {
        height: 400px;
    }

    .map-layer-control {
        bottom: 10px;
        left: 10px;
    }

    .layer-control-button,
    .layer-control-menu {
        min-width: 150px;
    }

    .layer-control-button {
        padding: var(--spacing-sm);
    }
}
