.temperature-info {
    display: flex;
    align-items: center;
    gap: var(--spacing-lg);
    padding: var(--spacing-lg);
}

.weather-icon-large {
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.weather-icon-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.temperature-display {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.temperature-value {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    line-height: 1;
}

.real-feel {
    font-size: var(--font-size-md);
    color: var(--color-text-secondary);
}

@media (max-width: 480px) {
    .weather-icon-large {
        width: 80px;
        height: 80px;
    }
}
