.activities-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-md);
}

.activities-column {
    display: flex;
    flex-direction: column;
}

@media (max-width: 768px) {
    .activities-grid {
        grid-template-columns: 1fr;
        gap: var(--spacing-lg);
    }
}
